// import Backbone from 'backbone'
import tmpl from '../templates'
import SkuList from '../collections/skuList'
import Sku from '../models/sku'

var app = upi.namespace('apps.productpage');

/* Treat SkuItemView as a layout Controller with child views */
var skuItemController = Backbone.View.extend({

    el: '#body-content',

    kitContentsTemplate: _.template(tmpl.skuItemDetails),

    initialize: function() {

        this.model = new Sku;

        this.$kitContents = $('#productKitContents');

        this.listenTo(this, 'sku:updated', this.render);
        this.listenTo(app.mediator, 'set:sku', this.setSku);
        this.listenTo(app.mediator, 'unset:sku', this.unsetSku);
    },

    render: function() {
        // render kitContents
        var contentsHtml = this.kitContentsTemplate(this.serializeData());
        this.$kitContents.html(contentsHtml);

        this.toggleSupplementaryContent();
    },

    serializeData: function() {
        function formatContentsString(str) {
            return str.replace(/-{1}/, 'x');
        }

        return {
            kitContents: this.model.get('kitContents').map(formatContentsString),
            shelfLife: this.model.get('shelfLife').map(formatContentsString)
        };
    },

    setSku: function(sku) {
        var prodName;

        sku = sku || new Sku; // pass empty Sku to clear panel if selection not present

        // make sure we have a sku and not already selected before sending tracking event
        if (sku && !sku.get('selected') && sku.get('id') !== 'noop') {
            app.dataLayer.setProductView(sku).send();
        }

        this.toggleSelect(sku);
        this.model = sku;
        this.trigger('sku:updated');
    },

    unsetSku: function() {
        this.model.select(false);
        this.model = new Sku; // id:noop
    },

    getQuantity: function() {
        return app.priceModuleView.getQuantity();
    },

    toggleSelect: function(sku) {
        // unset previous selection
        this.model.select(false);

        // if sku not null, set as current selection
        if (sku) {
            sku.select(true);
        }
    },

    toggleSupplementaryContent: function() {
        // only update supplementary content if sku is in "selected" state
        if (this.model.get('selected')) {
            this.togglePrintingMessage();
            this.updateImageCollection();
        };
    },

    togglePrintingMessage: function() {
        app.mediator.trigger('toggle:customprinting', this.model.get('isCustomPrintable'));
    },

    updateImageCollection: function() {
        var imageCollection = this.model.get('images');
        if (imageCollection.length) {
            app.imageCollection.reset(imageCollection);
        }
    },

    close: function() {
        this.undelegateEvents();
        this.stopListening(this.model);
        this.stopListening(app.mediator);
    }

});

export default skuItemController;



