// import Backbone from 'backbone';
import tmpl from '../../templates';
import Sku from '../../models/sku'


var app = upi.namespace('apps.productpage');

var ProductImageModuleView = Backbone.View.extend({

    el: '#productImageWrapper',

    template: _.template(tmpl.productImageModule),

    initialize: function(opt) {
        this.activeSku = opt.sku;

        this.listenTo(this.collection, 'reset', this.render, this);
        this.listenTo(this.activeSku, 'change:id', this.render);
        this.listenTo(app.mediator, 'set:sku', this.setSku);

        if (this.collection.models.length) {
            this.render();
        }

    },

    setSku: function(sku) {
        sku = sku || new Sku;
        this.activeSku.set(sku.attributes);
    },

    didMount: function() {
        // FIXME --> corpus is not imported properly (likely Backbone importing own instance of jQuery)
        $( this.$el ).corpus('tabs carousel');
    },

    render: function() {
        this.$el.html(this.template(this.serializeData()));
        this.didMount();
        return this;
    },

    serializeData: function() {
        var isNew = false,
            hasPromo,
            promo;

        if (this.activeSku) {
            isNew = this.activeSku.get('isNew');
            promo = this.activeSku.getPromotion();
            if (promo)
                hasPromo = true;
            }

        return {isNew: isNew, hasPromo: hasPromo, images: this.collection.models};
    },

    close: function() {
        this.undelegateEvents();
        this.stopListening(app.mediator);
        this.stopListening(this.collection);
        this.remove();
    }

});

export default ProductImageModuleView;



