import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.KITTYPE_API,
    responseType: 'text'
} );

const KittypeService = {

    getKitBySku( sku ) {
        return svc.request( `/GetKitTypeByItemNumber/${sku}` )
            .then( ServiceProvider.responseData );
    },

    getKitTypes( productURL ) {
        return svc.request( '/GetKitTypes', {params: {productURL}} )
            .then( ServiceProvider.responseData );
    }
};

export default KittypeService;














