import assign from 'object-assign';
import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider({
    baseURL: upi.conf.svc.ECOMMERCE_API,
    cache: true
});

/** SkuItem service */
const skuModel = {
    'InStock': true,
    'CurrencyCode': null,
    'CurrencyCountry': null,
    'CustomerPrice': 0,
    'ListPrice': 0,
    'StandardPrice': 0,
    'ForecastQuantity': null,
    'HasCustomPrint': false,
    'IsFormulary': false,
    'IsOrderable': true,
    'IsWebOrderable': true,
    'IsWebVisible': true,
    'KitName': '',
    'KitContents': [],
    'ShelfLife': [],
    'ProductFamily': '',
    'ProductName': '',
    'ProductSubFamily': '',
    'ProductSubTitle': '',
    'Sku': '',
    'UnitSize': ''
};

const SkuItemService = {
    getModel() {
        return Object.makeCopy( skuModel );
    },

    fetch( skus ) {
        // only make request if there is something in the skus string
        if ( !skus ) {
            return Promise.reject( [] );
        }

        if ( skus instanceof Array ) {
            skus = skus.toString();
        }

        return svc.get( '/getItemInfoJson',
            {
                params: {skus},
                cacheForUser: true
            } )
            .then( ( {data} ) => {
                // merge sku data from api with default skuModel => return new response
                data.Items = data.Items.map( item => ({...skuModel, ...item}) );
                return data;
            } )
    },

    getByAvailability( skuList ) {
        return this.fetch( skuList )
            .then( data => data.Items.filter( sku => sku.IsOrderable ) );
    },

    getRecommendations( sku ) {
        return svc.get( '/getSkuAssoc_Json', { params: {sku} } )
            .then( ServiceProvider.responseData );
    }
};

// todo: deprecate upi.namespace( 'SkuItem' ) once CartService has been refactored
export default assign( upi.namespace( 'SkuItem' ), SkuItemService );



