// import Backbone from 'backbone';

import ConfigurationListView from './configurationListView';
import Product from '../models/product';
import ConfigurationList from '../collections/configurationList';
import SkuList from '../collections/skuList';
import OptionGroupList from '../collections/optionGroupList';
import OptionDetailList from '../collections/optionDetailList';
import ImageList from '../collections/imageList';
import PromotionList from '../collections/promotionList';
import ValidationErrorController from './validationErrorController';
import SkuItemController from './skuItemController';
import OptionGroupListView from './optionGroupListView';
import PriceModuleView from './modules/priceModule';
import ProductImageModuleView from './modules/productImageModule';
import PromotionModuleView from './modules/promotionModule';
import Events from '../../modules/core/Events';

import {Cart} from '../../modules/cart/Cart';

var app = upi.namespace( 'apps.productpage' );

var AppController = Backbone.View.extend( {
    el: '#body-content',

    events: {
        'keypress .qty': 'handleAddToCart',
        'click #addSku': 'handleAddToCart',
        'mouseenter #addSku': function ( e ) {
            this.preFlightValidation( {onError: 'showhint'} );
        }
    },

    initialize: function ( dataLayer ) {
        var self = (app.appController = this);

        self.$el = $( self.el );
        self.$el.removeClass( 'initializing' );

        app.dataLayer = dataLayer;

        app.product = app.ProductModel ? new Product( app.ProductModel ) : null;
        app.historyStateIndex = 0;

        // verify we have a working configuration, fail fast
        if ( !app.product || !app.product.get( 'skus' ) ) {
            this.$el.addClass( 'degraded' );
            return;
        }

        self.initializeCollections( app );

        $( document ).ready( function () {
            self.initializeViews( app );
            self.bindEvents( app );
            self.initializeConfigurator( app );
            self.renderPromotions( app );

            var select = app.product.get( 'select' );
            self.loadSelection( select );
        } );

        return this;
    },

    loadSelection: function ( select ) {
        if ( select ) {
            if ( 'sku' in select ) {
                this.selectSku( select['sku'] );
            }
            else if ( 'group' in select ) {
                this.selectConfiguration( select['group'] );
            }
        }

        app.historyStateIndex = 0; // reset
    },

    initializeCollections: function ( app ) {
        // Initialize Models
        app.skuList = new SkuList( app.product.get( 'skus' ) );
        app.priceModuleSkuList = new SkuList();
        // depend2s on app.product & app.skuList

        app.promotionList = new PromotionList( app.product.get( 'promotions' ) );
        app.optionDetailList = new OptionDetailList(
            app.product.get( 'optionDetails' )
        );
        //                app.optionGroupList = new OptionGroupList( app.product.get( 'optionGroups' ) );
        app.imageCollection = new ImageList( app.product.get( 'images' ) );
    },

    initializeViews: function ( app ) {
        app.validator = new ValidationErrorController();

        // initialize configurator
        app.priceModuleView = new PriceModuleView( {
            collection: app.priceModuleSkuList
        } );
        app.skuItemController = new SkuItemController();
        app.productImageModuleView = new ProductImageModuleView( {
            sku: app.skuItemController.model,
            collection: app.imageCollection
        } );
    },

    bindEvents: function ( app ) {
        // handle back button
        $( window ).on( 'popstate', function () {
            if ( 0 !== app.historyStateIndex ) {
                window.history.go( app.historyStateIndex );
            }
        } );

        // update:route events should be triggered as early as possible before any views are rendered
        app.mediator.bind( 'update:route', this.handleRouteChange, this );
        app.mediator.bind( 'tooltip:addsku', this.showHint, this );

        this.listenTo( app.mediator, 'set:skulist', function ( skuList ) {
            app.priceModuleSkuList.reset( skuList );
        } );
    },

    scrollToNextTarget: function () {
        var scrollTarget,
            configuration = app.appController.getCurrentSelection(),
            optionGroupListView =
                app.configurationListView.configurationItemViews[configuration.cid]
                    .optionGroupListView;

        if ( configuration.hasGroups() ) {
            scrollTarget = app.configurationListView.getNextTarget();
        }
        else if ( optionGroupListView ) {
            scrollTarget = optionGroupListView.getNextTarget() || '#skuItemDetails';
        }
        else {
            scrollTarget = '#skuItemDetails';
        }

        $( scrollTarget || document.body ).scrollToElement();
    },

    initializeConfigurator: function ( app ) {
        // refactor: SRP
        var configurations = app.product.get( 'configurations' ),
            optionGroups = app.product.get( 'optionGroups' );

        // single sku configurations
        if ( 1 === app.skuList.length ) {
            var sku = app.skuList.first();
            this.setupSku(
                sku // if configuration collection exists
            );
        }
        else if ( configurations && configurations.length ) {
            this.setupConfigurations(
                configurations // else if options exist, only render applicable option groups
            );
        }
        else if ( optionGroups && optionGroups.length ) {
            this.setupOptionGroups( optionGroups );
        }
    },

    setupConfigurations: function ( configurations ) {
        app.configurationList = new ConfigurationList( configurations );
        app.configurationListView = new ConfigurationListView( {
            collection: app.configurationList
        } );
    },

    setupOptionGroups: function ( optionGroups ) {
        app.optionGroupList = new OptionGroupList( optionGroups, {
            skuList: new SkuList( app.skuList.models )
        } );

        app.optionGroupListView = new OptionGroupListView( {
            el: '#productConfiguration',
            collection: app.optionGroupList
        } );

        this.bindOptionGroupListView();
        app.optionGroupListView.render();
    },

    bindOptionGroupListView: function () {
        // handle option invalidations, image collection should default to brand image collection
        app.optionGroupListView.listenTo(
            app.optionGroupList,
            'option:invalidated',
            function () {
                app.imageCollection.reset( app.product.get( 'images' ) );
            }
        );

        // handle option:change when only configuration
        app.optionGroupListView.listenTo(
            app.optionGroupList,
            'change:option',
            function () {
                var options = app.optionGroupList.getSelected(),
                    skuList = this.collection.skuList,
                    sku = skuList.getSkuByOptions( options );

                app.mediator.trigger( 'set:skulist', skuList.models );
                app.mediator.trigger( 'set:sku', sku ); // note: can be undefined
            }
        );
    },

    setupSku: function ( sku ) {
        app.mediator.trigger( 'set:skulist', sku );
        app.mediator.trigger( 'set:sku', sku );
    },

    renderPromotions: function ( app ) {
        var promoViews = $( '<div />' );

        app.promotionList.each( function ( promo ) {
            var pv = new PromotionModuleView( {model: promo} );
            promoViews.append( pv.$el );
        } );

        $( '#promoWrapper' ).html( promoViews );
    },

    selectSku: function ( skuId ) {
        var sku = app.skuList.find( {id: skuId} ), configurationId, optionList;

        if ( sku ) {
            // if a configuration group is present then trigger selection from the top-level
            if ( (configurationId = sku.get( 'groupId' )) ) {
                this.selectConfiguration(
                    configurationId,
                    skuId // else apply selection from the optionsList level
                );
            }
            else if ( (optionList = sku.get( 'options' )) ) {
                this.selectOptions( optionList );
            }
            // set the current sku selection within the priceModule
            app.mediator.trigger( 'set:sku', sku ); // <-
        }
    },

    selectOptions: function ( skuOptions ) {
        app.mediator.trigger( 'set:options', skuOptions );
    },

    selectConfiguration: function ( configurationId, skuId ) {
        app.mediator.trigger( 'set:configuration', configurationId, skuId );
    },

    getCurrentSelection: function () {
        return app.optionGroupList
            ? app.optionGroupList
            : app.configurationList ? app.configurationList.current() : 'noop';
    },

    handleRouteChange: function ( selection ) {
        var route = '';

        if ( _.has( selection, 'skuList' ) ) {
            if ( selection.get( 'type' ) === 'sku' ) {
                route = '?sku=' + selection.get( 'skuId' );
            }
            else {
                route = '?group=' + selection.get( 'groupId' );
            }
        }
        else {
            route = '?sku=' + selection.get( 'id' );
        }

        if ( 'pushState' in window.history ) {
            app.historyStateIndex -= 1;
            window.history.pushState( {}, window.title, route );
        }
    },

    preFlightValidation: function ( opt ) {
        opt = opt || {};

        var dfd = $.Deferred(),
            current = this.getCurrentSelection();

        if ( 'noop' === current ) {
            dfd.resolve();
        }
        else if ( current ) {
            current.validate( opt ) ? dfd.resolve() : dfd.reject();
        }
        else {
            var err = {
                error: 'no configuration',
                message: 'Please select an option',
                el: $( '.option-set:first .group-label' )
            };

            dfd.reject( err );

            switch ( opt.onError ) {
                case 'showerror':
                    app.validator.trigger( 'error:validation', err );
                    break;
                case 'showhint':
                    //                            console.log( 'show hint:', err.message );
                    app.mediator.trigger( 'tooltip:addsku', err );
                    break;
                default:
                // fail silent
            }

            //console.log( '@AppController validation failed: no configuration selected' );
        }

        return dfd.promise();
    },

    handleAddToCart: function ( e ) {
        if ( 'keypress' === e.type && !Events.is_enter_key( e ) ) {
            return true;
        }

        e.preventDefault();
        var self = this;

        return this.preFlightValidation( {onError: 'showerror'} )
            .then(
                () => {
                    return self.addToCart(
                        app.skuItemController.model.toJSON(),
                        app.skuItemController.getQuantity()
                    )
                },
                () => {
                    // trigger analytic events
                    app.dataLayer.addedWithoutSelection();

                    if ( Modernizr.mq( upi.mq.min.screenMedium ) ) {
                        // scroll to top of configurator if large device
                        app.appController.$el.scrollToElement();
                    }
                    else {
                        // scroll to error if mobile
                        app.appController.scrollToNextTarget();
                    }
                }
            )
    },

    transformSku ( sku ) {

        return {
            'CurrencyCode': sku.currencyCode,
            'CurrencyCountry': sku.currencyCountry,
            'CustomerPrice': sku.customerPrice,
            'QuantityBreaks': Array.isArray( sku.quantityBreaks ) &&
            sku.quantityBreaks.map( ( {quantity, price} ) => ({Quantity: quantity, Price: price}) ),
            'EstimatedShipDate': sku.expectedShipDate,
            'InStock': sku.inStock,
            'IsNew': sku.isNew,
            'IsFormulary': sku.isFormulary,
            'IsOrderable': sku.isOrderable,
            'IsWebOrderable': sku.isWebOrderable,
            'KitContents': sku.kitContents,
            'KitImgSrc': sku.images,
            'KitName': sku.itemLongName,
            'ProductName': sku.itemName,
            'ShelfLife': sku.shelfLife,
            'Sku': sku.id,
            'id': sku.id
        };
    },

    addToCart: function ( sku, qty ) {
        var skuModel = {
            ...this.transformSku( sku ),
            Quantity: qty
        };

        this.processing( true );
        return Cart.add_to_cart( skuModel, true )
            .catch( err => {
                this._onAddToCartFailed();
            } )
            .then( result => {
                this.processing( false );
                return result
            } )
    },

    _onAddToCartFailed: err => {
        app.mediator.trigger( 'cart:itemnotadded', err );
    },

    processing: function ( isProcessing ) {
        this.isProcessing = isProcessing;
        $( '#addSku' ).toggleClass( 'processing', isProcessing );
    },

    showHint: function ( err ) {
        $( '#addSku' ).showHint( err.message + ' to the left before adding to cart', {
            placement: 'left'
        } );
    },

    close: function () {
        app.mediator.unbind( 'tooltip:addsku' );
        this.stopListening( app.mediator );

        app.validator.clearChildViews();
        app.priceModuleView.close();
        app.skuItemController.close();
        app.productImageModuleView.close();

        delete app.product;
        delete app.promotionList;
        delete app.skuList;
        app.optionDetailList.destroy();
        delete app.optionDetailList;
        delete app.imageCollection;
        delete app.priceModuleSkuList;

        this.remove();
    }
} );

export default AppController;



