import '../../css/product-page-configurations.scss';

import AppController from './views/appController';
import AppRouter from './routes/appRouter';
import PageController from './views/pageController';

// Analytics tracking
import dataLayer from './dataLayer';

// productpage.js auto init $.ready
require( 'productpage.js' );

var app = upi.namespace( 'apps.productpage' ),

    initialize = function () {
        $( '#body-content' ).addClass( 'initializing' );

        app.mediator = _.extend( {}, Backbone.Events, {
            willTrigger: function ( evName ) {
                return function () {
                    this.trigger( evName, arguments );
                }.bind( this );
            }
        } );

        AppRouter.initialize();

        app.pageController = new PageController();
        app.appController = new AppController( dataLayer );
    },

    destroy = function () {
        app.mediator.unbind();
        app.pageController.close();
        app.appController.close();

        delete app.mediator;
        delete app.pageController;
        delete app.appController;
    };

export default {
    initialize,
    destroy
};



