import KittypeService from './services/KittypeService'

jQuery.fn.submitHandler = function () {
    //capture products request info submit
    var omniture = {};
    omniture.events = 'event2';

    if ( $( '#Interested_x0020_In' ).val() == 'Product Line' ) {
        $( '#successIntro' ).html(
            $( '#productInfo' )
                .find( '.thumbnail' )
                .clone()
                .wrap( '<p>' )
                .parent()
                .html() +
            '<p id="popupIntro">' +
            $( '#ThankYouText' )
                .html()
                .replace(
                    '[product name]',
                    '<strong>' +
                    $( '#productTitle' ).text().trim() +
                    '</strong>'
                ) +
            '</p>'
        );
        omniture.eVar2 = $( '#productTitle' ).text().trim();
    }
    else {
        $( '#successIntro' ).html(
            $( '#productInfo' )
                .find( '.thumbnail' )
                .clone()
                .wrap( '<p>' )
                .parent()
                .html() +
            '<p id="popupIntro">' +
            $( '#ThankYouText' )
                .html()
                .replace(
                    '[product name]',
                    '<strong>' +
                    $( '#Product' + formFieldSuffix ).val() +
                    '</strong>'
                ) +
            '</p>'
        );
        omniture.eVar5 = $( '#Product' + formFieldSuffix ).val();
    }

    try {
        s.t( omniture );
    }
    catch ( error ) {
    }

    $.colorbox.resize();
};

(function () {

    // scroll to location #hash
    const anchorId = location.hash;
    $( window ).on( 'load', function () {
        const overviewItems = $( '.productFamilyOverview' );
        if ( anchorId ) {
            let selectedItem = overviewItems.find( anchorId );
            if ( selectedItem[0] ) {
                // open the accordion and scroll to element
                setTimeout( () => {
                    selectedItem.find( '.accordion-label:first' ).click();
                    selectedItem.scrollToElement();
                }, 250 );
            }
        }
    } );

    /* Product Line and Kit Types Lightbox */
    $( document ).on( 'click', '.requestInfo', function ( e ) {
        e.preventDefault();
        var $target = $( e.target ), skuId = $target.data( 'skuid' );

        // request infor for product family or a sku item
        skuId && 'noop' !== skuId
            ? requestInfoKitType( skuId )
            : updateProductInfo();

        $.colorbox(
            upi.settings.defaultColorbox( {
                onLoad: function () {
                    $( '#successMessage' ).remove();
                    $( '#cboxTitle' ).remove();
                },
                onComplete: function () {
                    $( '#popupFormFields :input[type=\'text\']:first' ).focus();
                    $.colorbox.resize();
                },
                width: 650,
                inline: true,
                href: '#requestInfoForm'
            } )
        );
    } );

    $( document ).ready( function () {
        /* initialize tooltips */
        var mainContent = $( '#body-content' );

        $( 'input:radio[name="RequestType"]' ).on( 'change', function () {
            $( '#Product' + formFieldSuffix ).val(
                $( 'input:radio[name="RequestType"]:checked + label' ).text()
            );
        } );

        // business logic
        // if China, remove facebook like buttons
        if ( 'zh-cn' === upi.Locale.get() ) {
            $( '.product-social' ).remove();
        }

        // trigger leftnav dropdown
        var navBetaDrop = $( '#navigationBeta.dropdown' );
        if ( navBetaDrop[0] ) {
            $( '#crumbBarNav' ).dropDownMenu( {
                // drop down is placed outside trigger node
                menuSelector: null,
                outerMenuSelector: navBetaDrop.selector,
                openDuration: 0,
                openTimeout: 150,
                closeTimeout: 100
            } );
        }

        /* Color picker for product category pages in edit mode. */
        if ( $( 'input[title=\'Category Color\']' ).length != 0 ) {
            $( 'input[title=\'Category Color\']' )
                .ColorPicker( {
                    onSubmit: function ( hsb, hex, rgb, el ) {
                        $( el ).val( hex );
                        $( el ).ColorPickerHide();
                    },
                    onBeforeShow: function () {
                        $( this ).ColorPickerSetColor( this.value );
                    }
                } )
                .bind( 'keyup', function () {
                    $( this ).ColorPickerSetColor( this.value );
                } );
        }

        // Shared International product page
        /* Product Detail LightBox Code */
        /* Distributors Lightbox */
        $( '.productBuy' ).colorbox(
            upi.settings.defaultColorbox( {
                onLoad: function () {
                    $( '#cboxTitle' ).remove();
                },
                width: 800,
                inline: true,
                href: '#distributorDiv'
            } )
        );

        /*omniture code - capture 'where to buy' for product and kit types*/
        $( '#productBuy' ).click( function () {
            //capture products distributors
            var omniture = new Object();
            omniture.events = 'event3';
            omniture.eVar2 = $( '#productTitle' ).text().trim();
            s.t( omniture );
            /*end omniture code*/
        } );

        $( 'div.itemActions a.productBuy' ).click( function () {
            //capture kit type distributors info
            var omniture = new Object();
            omniture.events = 'event3';
            omniture.eVar5 = $( this )
                .parent()
                .next()
                .next()
                .find( '#itemTitle' )
                .text();
            s.t( omniture );
            /*end omniture code*/
        } );
        /*end omniture code*/

        $( '#colorbox' ).detach().appendTo( '#aspnetForm' );
        $.colorbox.resize();
    } );

    /* Update PRODUCT LINE request info lightbox before display */
    function updateProductInfo() {
        $( '#productImage' ).find( 'li.selected img, img:first' );
        var image =
            $( '#productImage li.selected img:first' )[0] ||
            $( '#productImage img:first' )[0];

        $( '#form-' + formFieldSuffix ).find( '.mainForm' ).show();
        /* Show the form in the lightbox each time the lightbox is shown (it may have been hidden upon success */
        $( '#' + formFieldSuffix + '_SubmitButton' + formFieldSuffix ).show();
        $( '#form-' + formFieldSuffix ).find( '.successMessage' ).hide();
        $( '.mainForm :input' ).parent().removeClass( 'error' );
        $( '.mainForm :input' ).val( '' ).trigger( 'change' );
        /* Clear the inputs each time the lightbox is shown. */
        $( '.formError' ).html( '' );
        /* Hide any previous error messages */
        $( '#popupIntro' ).hide();
        $( '#productInfo' )
            .html(
                `<img class="thumbnail" src="${(image ? image.getAttribute( 'src' ) : upi.Images._404)}"/>` +
                $( '#ProductLineText' )
                    .html()
                    .replace(
                        '[product name]',
                        `<strong>${ $( '#productTitle' ).text().trim() }</strong>`
                    )
            );

        $( '#Title' + formFieldSuffix ).val( 'Request Product Information' );
        $( '#Product_x0020_Line' + formFieldSuffix ).val(
            $( '#productTitle' ).text().trim()
        );
        $( '#Interested_x0020_In' ).val( 'Product Line' );
        $.colorbox.resize();

        //capture product line request info click
        var omniture = new Object();
        omniture.events = 'event1';
        omniture.eVar2 = $( '#productTitle' ).text().trim();
        s.t( omniture );
        /*end omniture code*/
    }

    /* Grab Kit Type Info and Update KIT TYPE request info lightbox before display */
    function requestInfoKitType( sku ) {

        KittypeService.getKitBySku( sku )
            .then( data => {
                $( data ).find( 'KitType' ).each( function () {
                    var $thumbnailSRC = $( $( this ).find( 'Image' ).text() ).attr( 'src' ),
                        $thumbnailALT = '',
                        $form = $( '#form-' + formFieldSuffix ),
                        $inputs = $( '.mainForm :input' ),
                        $submitButton = $( '#' + formFieldSuffix + '_SubmitButton' + formFieldSuffix ),
                        $formError = $( '.formError' );

                    if ( $thumbnailSRC != null ) {
                        $thumbnailSRC = 'src=\'' + $thumbnailSRC + '\''; //don't add an img src if the image doesn't exist.
                        $thumbnailALT = $( this ).find( 'Title' ).text();
                    }

                    $form.find( '.mainForm' ).show();
                    /* Show the form in the lightbox each time the lightbox is shown (it may have been hidden upon success */
                    $submitButton.show();
                    $form.find( '.successMessage' ).hide();
                    $inputs.parent().removeClass( 'error' );
                    $inputs.val( '' );
                    /* Clear the inputs each time the lightbox is shown. */
                    $formError.html( '' );

                    /* Hide any previous error messages */
                    $( '#popupIntro' ).show().html(
                        $( '#KitTypeText' )
                            .html()
                            .replace(
                                '[product name]',
                                `<strong>${ $( this ).find( 'Title' ).text() }</strong>`
                            )
                    );
                    $( '#productInfo' ).html(
                        `<img ${$thumbnailSRC} class="thumbnail" alt="${$thumbnailALT}" style="height:90px;" />
                        <ul>
                            <li>
                                <input type="radio" name="RequestType" value="Product Line" id="radioProductLine"/>
                                <label for="radioProductLine">${$( this ).find( 'ProductTitle' ).text()}</label>
                            </li>
                            <li>
                                <input type="radio" name="RequestType" value="KitType" id="radioKitType" checked/>
                                <label for="radioKitType">
                                    ${$( this ).find( 'Title' ).text()}
                                    -
                                    #${$( this ).find( 'ItemNumber' ).text()}
                                </label>
                            </li>
                        </ul>` );
                    $( '#Title' + formFieldSuffix ).val(
                        'Request Kit Type Information'
                    );
                    $( '#Product_x0020_Line' + formFieldSuffix ).val(
                        $( this ).find( 'ProductTitle' ).text()
                    );
                    $( '#Product' + formFieldSuffix ).val(
                        $( this ).find( 'Title' ).text() +
                        ' (' +
                        $( this ).find( 'ItemNumber' ).text() +
                        ')'
                    );
                    $( '#Interested_x0020_In' + formFieldSuffix ).val( 'Kit Type' );
                } );

                $.colorbox.resize();

                //capture products
                s.t( {
                    events: 'event1',
                    eVar5: $( '#Product' + formFieldSuffix ).val()
                } );
                /*end omniture code*/
            } )

    }
})();



