import SkuItemService from '../services/SkuItemService';
import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.ECOMMERCE_API
} );

/**
 * sanatizes data returned from svc
 * @return {Object} - callback config { success: {Function} }
 */
function cleanResponse( data ) {
    // apply sane Sku defaults for templates
    data.CartItems = data.CartItems || [];

    for ( var i = 0, l = data.CartItems.length; i < l; i++ ) {
        data.CartItems[i] = Object.extend( SkuItemService.getModel(), data.CartItems[i] );
    }

    return data;
}

export default {

    add_item( items ) {
        return svc.post( '/addCartItemsJson', {params: items} )
            .then( resp => cleanResponse( resp.data ) );
    },

    fetch( params ) {
        return svc.get( '/getCartJson', {params} )
            .then( resp => cleanResponse( resp.data ) );
    }
};














