// import Backbone from 'backbone';
import User from '../../../modules/User';
import tmpl from '../../templates';
import priceBreakdownModuleView from './priceBreakdownModule';

// import 'jquery.toggleqtyselect';

var app = upi.namespace( 'apps.productpage' );

var stripSpecialCharacters = (function stripSpecialCharacters( str ) {
    return str.replace( /[\u00AE|\u2122]/gi, '' );
}).memoize();

var PriceModuleView = Backbone.View.extend( {

    el: '#skuItemDetails',

    templateSku: _.template( tmpl.priceModuleSku ),
    templateSkuList: _.template( tmpl.priceModuleSkuList ),
    templateEmpty: _.template( tmpl.priceModuleEmpty ),

    // todo: garbage collect views/childViews
    events: {
        'keyup .qty': 'handleQtyChange',
        'change .qty': 'handleQtyChange'
    },

    prevQty: 0,

    initialize: function () {

        this.priceBreakdownModuleView = new priceBreakdownModuleView();

        this.listenTo( this.collection, 'change reset', this.handleChange );
        this.listenTo( this.priceBreakdownModuleView.collection, 'change:selected', this.handleQuantityBreakChange );
        this.listenTo( app.mediator, 'cart:itemadded', this.resetView );
        this.listenTo( app.mediator, 'cart:itemnotadded', this.showAddToCartError, this );
        this.listenTo( app.mediator, 'configuration:select', function ( configuration ) {
            this.configuration = configuration;
        } );

        this.render();
    },

    getQuantity: function () {
        // if value NaN return 1
        var qty = parseInt( this.$el.find( '#skuQty' ).val().trim(), 10 );
        return (qty && typeof qty === 'number')
            ? qty
            : 1;
    },

    handleChange: function ( data ) {
        /*
         * if a single Model triggered event, pluck that item from this.SkuList to pass data to 'sku' view
         * else if a Collection render the Range view
         *   if a Collection but only has a single item, pluck that item from this.Skulist and send to 'sku' view
         * else if none of the above default to Empty view
         */
        this.prevQty = 0;

        if ( data instanceof Backbone.Model ) {

            var sku = data,
                $tmpl;
            if ( sku.get( 'selected' ) ) {
                var skuData = this.serializeData( sku );
                var priceModuleTmpl = $( this.templateSku( skuData ) );
                // will hide pricing for items with estrictions
                this.priceBreakdownModuleView.setPriceRestrictions( skuData.hasPriceRestrictions );
                this.priceBreakdownModuleView.collection.reset( sku.serializePriceBreaks() );

                this.render( priceModuleTmpl );

                return;
            }
        }
        else if ( data instanceof Backbone.Collection ) {
            if ( 1 < this.collection.length ) {
                // if more than one item in collection then render as PriceRange view
                return this.render( this.templateSkuList( this.serializeData() ) );
            }
            else if ( 1 === this.collection.length ) {
                // todo: preselect only option?
                // console.log( 'is Collection ...with a single item' );
                return this.render( this.templateEmpty() );
            }
        }
    },

    handleQtyChange: function () {
        var qty = this.getQuantity();

        if ( !isNaN( qty ) && qty !== this.prevQty ) {
            this.prevQty = qty;
            this.priceBreakdownModuleView.collection.trigger( 'update:qty', qty );
        }
    },

    render: function ( tmpl ) {
        this.$el.html( tmpl || this.templateEmpty() );

        // append childView
        this.$el.find( '.priceBreakdownsWrapper' ).html( this.priceBreakdownModuleView.$el );

        $( this.$el ).find( '#skuQty' ).toggleQtySelect();

        return this;
    },

    serializeData: function ( sku ) {
        var data;

        if ( sku ) {
            var m = this.collection.get( sku );
            data = {
                'isLogged': User.isLogged(),
                'hasPriceRestrictions': m.hasPriceRestrictions(),
                'skuId': m.get( 'id' ).toString().replace( /-$/, '' ),
                'groupId': m.get( 'groupId' ),
                'itemName': stripSpecialCharacters( m.get( 'itemName' ) ),
                'optionName': m.getOptionName(),
                'kitContents': m.get( 'kitContents' ),
                'options': m.get( 'options' ),
                'currencyCode': m.get( 'currencyCode' ),
                'currencyCountry': m.get( 'currencyCountry' ),
                'customerPrice': (m.get( 'customerPrice' ) > 0)
                    ? m.toCurrency( 'customerPrice' )
                    : 'Price Unavailable',
                'salePrice': (m.get( 'salePrice' ) > 0)
                    ? m.toCurrency( 'salePrice' )
                    : 'Price Unavailable',
                // 'priceBreakdown': m.toCurrency( 'priceBreakdown' ),
                'quantityBreaks': m.toCurrency( 'quantityBreaks' ),
                'images': m.get( 'images' ),
                'unitSize': m.get( 'unitSize' ),
                'isOrderable': m.get( 'isOrderable' ),
                'isWebOrderable': m.get( 'isWebOrderable' ),
                'isFormulary': m.get( 'isFormulary' ),
                'isDomestic': upi.env.IS_ENUS,
                'inStock': m.get( 'inStock' ),
                'expectedShipDate': upi.Date.format( m.get( 'expectedShipDate' ), 'longName' ),
                'isNew': m.get( 'isNew' ),
                'isCustomPrintable': m.get( 'isCustomPrintable' ),
                'selected': m.get( 'selected' )
            };
        }
        else {
            data = {
                'isLogged': User.isLogged(),
                'hasPriceRestrictions': this.collection.hasPriceRestrictions(),
                'productName': stripSpecialCharacters( app.product.get( 'name' ) ),
                'configurationName': this.configuration
                    ? stripSpecialCharacters( this.configuration.get( 'name' ) )
                    : null,
                'priceRange': this.collection.getPriceRange()
            };
        }

        return data;
    },

    handleQuantityBreakChange: function ( priceBreak ) {
        // only handle changes on currently selected priceBreak - :change on previous priceBreak falls through
        // make sure the priceBreak is a valid price
        if ( priceBreak.get( 'selected' ) && priceBreak.get( 'price' ) > 0 ) {
            var sku = this.collection.getSelectedSku();
            this.setPriceToBreakPoint( priceBreak );
            this.toggleDiscountedPrice( priceBreak.get( 'price' ) < sku.get( 'customerPrice' ) );
        }
    },

    setPriceToBreakPoint: function ( priceBreak ) {
        this.$el.find( '#productItemPriceValue' ).text( priceBreak.toCurrency( 'price' ) );
    },

    toggleDiscountedPrice: function ( isDiscounted ) {
        this.$el.find( '#productItemWasPriceValue' ).toggleClass( 'isDiscounted', isDiscounted );
    },

    showAddToCartError: function () {
        this.toggleError( true );
    },

    toggleError: function ( hasError ) {
        $( '#pm-cart-error' ).toggleClass( 'on', hasError );
    },

    resetView: function () {
        this.toggleError( false );
        this.$el.find( 'input' ).val( '' ).trigger( 'change' );
    },

    close: function () {
        this.stopListening( this.collection );
        this.stopListening( app.mediator );
        this.$el.off();
        this.$el.remove();
    }

} );

export default PriceModuleView;



