// $.toastr api - https://github.com/CodeSeven/toastr#toastr
// configure notifications
import 'toastr';

toastr.options = {
    'positionClass': 'toast-top-full-width',
    'hideDuration': 2000
};

export default {
    notify( msg ) {
        toastr.success( msg );
    },
    warn( msg ) {
        toastr.warning( msg );
    },
    info( msg ) {
        toastr.info( msg );
    },
    error( msg ) {
        toastr.error( msg );
    },

    prompt( fn ) {
        var args = [].slice.call( arguments, 1 ), self = this;

        $( 'body' ).upiDialog( upi.dialog.GENERIC_SERVICE_ERROR, {
                'ok': (typeof fn === 'function')
                    ? function () {
                        fn.apply( self, args );
                    }
                    : null
            }, {className: 'formError'}
        );
    }
}



