import assign from 'object-assign';
import Events from '../core/Events';
import User from '../User';
import CartService from '../../services/CartService';
import OptimisticStore from '../core/OptimisticStore';
import Notifier from '../../modules/ui/Notifier';
import {normalizeSku} from '../../util/cleanSku';
import find from 'lodash.find';

const DEFAULT_CART = {
    isLoaded: false,
    CartItems: [],
    CartTotals: null /*{OrderSubTotal: {Number}}*/
};

function _Cart() {
    const _instance = upi.namespace( 'Cart' );

    if ( _instance.initialized ) {
        return _instance;
    }

    this.initialized = true;
    this.store = new OptimisticStore( DEFAULT_CART );
    // when async updates to the store fail return the UI back to a consistent state
    this.store.bind( 'reconciled', ( e, newStore ) => {
        this.notifyAll( newStore );
        Notifier.error(
            `Sorry, there was a problem while trying to update your cart. Your cart has been returned to it's previous state` );
    } );

    return this;
}

_Cart.prototype = assign( {}, Events, {

    /**
     * Private methods
     */

    /**
     *
     * @param skuCollection
     * @param mergeStrategy
     * @return {function()}
     * @private
     */
    _handleTransaction( skuCollection, mergeStrategy ) {
        return () => {
            let {store, transaction} =
                this.store.updateAsync( store => mergeCart( store, skuCollection, mergeStrategy ) );

            this.notifyAll( store );
            this._syncCart( transaction, store.CartItems, skuCollection );

            return store;
        }
    },

    /**
     * talks to the actual Cart api
     * @param transaction
     * @param cartItemsState
     * @param skus
     * @private
     */
    _syncCart( transaction, cartItemsState, skus ) {
        // extract applied changes from cart
        let changes = skus.map( a => {
            const exists = find( cartItemsState, b => a.Sku === b.Sku );
            // if item from skus does not appear in "cartItemsState" that means it was removed so pass "delete" instructions
            // to api by sending "Quantity:0"
            return exists || {
                Quantity: 0,
                Sku: a.Sku
            };
        } );

        return CartService.add_item(
            {
                cartItems: changes.map( ( {Sku, Quantity} ) => ({Sku, Quantity}) )
            } )
            .then( transaction.success )
            .catch( transaction.fail );
    },

    /**
     * Public methods
     */


    /**
     * Fetch cart data from CartService api
     * @return {Object} - cart
     */
    fetch() {
        return CartService.fetch();
    },

    /**
     * Fetch cart data from CartService api then merge in local cart changes if cart is being fetched
     * for the first time
     * @return {Object} - cart
     */
    fetchAndMerge() {
        return CartService.fetch().then( cartData => {
            const state = this.store.getValue();

            // if the cart is already don't apply to state
            if ( state.isLoaded ) {
                return cartData;
            }

            // if fetching cart for the first time we need to check if anything has already been added to the local
            // cart state so it can be merged in with the server response. This handles scenarios where a page just loaded
            // and a user adds something to their cart before the initial cart fetch has been performed.
            // update local cart state
            return this.store.update( store => {
                let newCart = mergeCart( store, cartData.CartItems, cartAdditionsStrategy );
                return assign( {}, newCart, {isLoaded: true} );
            } );
        } )
    },

    /**
     *
     * @param skus
     * @param showConfirmation
     * @return {Promise.<TResult>}
     */
    add_to_cart( skus, showConfirmation ) {
        const skuCollection = sanitizeSkuCollection( skus );

        return Promise.resolve()
            .then( this._handleTransaction( skuCollection, cartAdditionsStrategy ) )
            .then( newCart => {
                let newCartAdditions = {
                    ...newCart,
                    isLogged: User.isLogged(),
                    itemAdditions: skuCollection
                };

                if ( showConfirmation ) {
                    this.trigger( 'quickadd:item', newCartAdditions );
                }

                return newCartAdditions;
            } )
    },

    /**
     *
     * @param skus
     * @return {Promise.<TResult>}
     */
    update_items( skus ) {
        const skuCollection = sanitizeSkuCollection( skus );

        return Promise.resolve()
            .then( this._handleTransaction( skuCollection, cartUpdatesStrategy ) )
    },

    /**
     *
     * @param cartItems
     * @return {number}
     */
    get_cart_item_count( cartItems ) {
        if ( !Array.isArray( cartItems ) ) {
            return 0;
        }

        return cartItems.reduce( ( acc, next ) => (acc += parseInt( next.Quantity, 10 ) || 0), 0 );
    },

    /**
     *
     * @param cart
     */
    notifyAll( cart ) {
        this.trigger( 'cart:countchange', {
            cartCount: this.get_cart_item_count( cart.CartItems )
        } );

        this.trigger( 'cart:updatecart', cart );
    }
} );

const instance = new _Cart();

upi.Cart = instance;

export let Cart = instance;

/**
 * Utilities
 */

/**
 *
 * @param cartItemAdditions
 * @param mergeStrategy
 * @returns {Object} - updates cart state
 */
function mergeCart( state, cartItemAdditions, mergeStrategy ) {
    if ( typeof mergeStrategy !== 'function' ) {
        throw new Error( `"mergeStrategy" param must be a function` )
    }
    return updateCart( state, {
        CartItems: mergeStrategy( state.CartItems, cartItemAdditions )
    } );
}

/**
 *
 * @param state
 * @param data
 * @return {{CartItems: (Array|*|module.exports.properties.CartItems|{type, minItems, maxItems, uniqueItems, items}|module.exports.properties.Orders.items.CartItems), CartTotals: {OrderSubTotal}}}
 */
function updateCart( state, data ) {
    return {
        ...state,
        CartItems: data.CartItems,
        CartTotals: {
            ...data.CartTotals,
            OrderSubTotal: calculateSubtotal( data.CartItems )
        }
    };
}

/**
 *
 * @param cartItems
 * @return {number}
 */
function calculateSubtotal( cartItems ) {
    if ( !Array.isArray( cartItems ) ) {
        return 0;
    }

    return cartItems.reduce( ( total, item ) => {
        const {Quantity, ListPrice, CustomerPrice, QuantityBreaks} = item,
            basePrice = ListPrice || CustomerPrice;
        let newPrice = getLineItemPrice( Quantity, basePrice, QuantityBreaks );

        if ( newPrice ) {
            total += newPrice * Quantity;
        }

        return total;
    }, 0 );
}

/**
 *
 * @param qty
 * @param basePrice
 * @param {Array[{Quantity,Price}]} qtyBreaks = Array of {Quantity, Price} objects
 * @return {Number|null} - return calculated price break or null
 */
export function getLineItemPrice( qty = 1, basePrice, qtyBreaks = [] ) {
    // if no basePrice (for restricted pricing when user is not logged in)
    if ( !basePrice || isNaN( basePrice ) ) {
        return null;
    }

    // return basePrice if no quantity breaks available
    if ( !qtyBreaks || qtyBreaks.length <= 0 ) {
        return basePrice;
    }

    return qtyBreaks
    // ensure qtyBreaks are sorted in numerical order
        .sort( ( a, b ) => parseInt( a.Quantity ) > parseInt( b.Quantity ) )
        // set price to highest available price break based on provided qty if less than basePrice -> default basePrice
        .reduce( ( price, next ) => {
            price = (qty >= next.Quantity && next.Price < price) ? next.Price : price;
            return price;
        }, basePrice );
}

/**
 * Apply a merge against 2 cart item collections, if SKu already exists in collection the Quantity will be incremented
 * by the specified amount
 * @param items - current cart items
 * @param addedItems - cart items to update
 * @returns {Array} - updated cart items
 */
function cartAdditionsStrategy( items, addedItems ) {
    if ( !Array.isArray( items ) ) {
        return [];
    }

    return addedItems.reduce( ( acc, next ) => {
        // if sku already in cart -> increment "Quantity"
        let exists = find( acc, i => i.Sku === next.Sku );
        if ( exists ) {
            return acc.map( cartItem => {
                if ( cartItem.Sku !== next.Sku ) {
                    return cartItem;
                }

                return {
                    ...cartItem,
                    Quantity: cartItem.Quantity + next.Quantity
                }
            } );
        }

        // else append to cart items
        return acc.concat( [assign( {}, next )] );
    }, items );
}

/**
 * Apply an update against 2 cart item collections, items who's Quantity <= 0 will be filtered from final results
 * @param items - current cart items
 * @param updatedItems - cart items to update
 * @returns {Array} - updated cart items
 */
function cartUpdatesStrategy( items, updatedItems ) {
    if ( !Array.isArray( items ) ) {
        return [];
    }

    return items
        .map( item => {
            let exists = find( updatedItems, i => i.Sku === item.Sku );
            if ( exists ) {
                return {
                    ...item,
                    Quantity: exists.Quantity
                };
            }

            return item;
        } )
        .filter( item => item.Quantity > 0 );
}

/**
 *
 * @param skus
 */
function sanitizeSkuCollection( skus ) {
    // todo: add SKU model validation to verify required fields are present
    if ( !Array.isArray( skus ) ) {
        skus = [skus];
    }

    // use "qty" if provided or remove by setting 0
    return skus.map( item => {
        let qty = parseInt( item.Quantity, 10 );

        return {
            ...item,
            Quantity: (typeof qty === 'undefined') ? 1 : qty,
            // ensure this sku s dashed to match api responses
            Sku: normalizeSku( item.Sku )
        };
    } );
}



