import assign from 'object-assign';
import Events from './Events';
import equal from 'deep-equal';

function OptimisticStore( data ) {
    this._pessimist = data;
    this._optimist = assign( {}, this._pessimist );
    this._queue = [];

    this.value = data;
}

function applyUpdate( update, store ) {
    if ( typeof update !== 'function' ) {
        throw new Error( '"update" must be a function.' );
    }

    // do not mutate store
    return update( assign( {}, store ) );
}

OptimisticStore.prototype = $.extend( {}, Events, {
    /**
     *
     * @return {*}
     */
    reconcile() {
        // no updates to apply
        if ( this._queue.length <= 0 ) {
            return this.value;
        }

        // rebuild resolved updates
        const
            storeBeforeUpdate = this._optimist,
            resolved_updates = [];

        // rollback to previous pessimist state
        this._optimist = this._pessimist;

        // NOTE: order of applying updates shouldn't matter as there should be no data dependency between async updates
        // if you have a data dependency, then you should be applying them synchronously using updateAsync's returned promise callbacks!
        for ( let i = this._queue.length - 1; i >= 0; i-- ) {
            let item = this._queue[i];

            /*console.log( 'Promise status', {
                pending: item.promise.pending,
                fulfilled: item.promise.fulfilled,
                value: item.promise.value
            } );*/

            if ( item.promise.pending ) {
                // if pending then only apply the updates to the optimist store but leave unresolved and in queue for
                // future calculations
                this._optimist = applyUpdate( item.update, this._optimist );
                continue;
            }
            else if ( item.promise.fulfilled ) {
                // if fulfilled then resolve to pessimist store and remove from queue
                this._optimist = item.update( assign( {}, this._optimist ), item.promise.value );
                this._pessimist = item.update( assign( {}, this._pessimist ), item.promise.value );
            }
            // remove any resolved update & push to resolved list
            this._queue.splice( i, 1 );
            resolved_updates.push( {
                succeeded: item.promise.fulfilled,
                value: item.promise.fulfilled
                    ? item.promise.value
                    : 'Transaction failed'
            } );
        }
        if ( !equal( storeBeforeUpdate, this._optimist ) ) {
            this.value = this._optimist;
            this.trigger( 'reconciled', this.value );
        }

        return this.value;
    },

    /**
     *
     * @param update
     * @return {{}}
     */
    updateAsync( update ) {
        const
            storeBeforeUpdate = this._optimist,
            transaction = {},
            queueItem = {
                update: update,
                promise: null
            };

        queueItem.promise = transaction._promise = new Promise( ( resolve, reject ) => {
            transaction.success = resolve;
            transaction.fail = reject;
        } );

        queueItem.promise.pending = true;

        // apply optimistic updates
        this._optimist = applyUpdate( update, this._optimist );

        // either resovle/reject the pessimist store depending on result of Promise
        queueItem.promise.then(
            () => {
                this._pessimist = applyUpdate( update, this._pessimist );
                // remove self from queue
                queueItem.promise.pending = false;
                queueItem.promise.fulfilled = true;
                queueItem.promise.value = this._pessimist;
                // console.log( 'Resolved store updates...' );
                this._queue.splice( this._queue.indexOf( queueItem ), 1 );
            },
            () => {
                queueItem.promise.rejected = true;
                queueItem.promise.pending = false;
                queueItem.promise.value = null;
                console.warn( 'Rejected store updates...' );
                this.reconcile();
            }
        );

        // store the optimist value for retrieval
        if ( !equal( storeBeforeUpdate, this._optimist ) ) {
            this.value = this._optimist;
            this.trigger( 'changed', this.value );
        }

        this._queue.push( queueItem );

        return {
            transaction,
            store: this.value
        };
    },

    /**
     *
     * @param update
     */
    update( update ) {
        let storeBeforeUpdate = this._optimist;

        this._pessimist = applyUpdate( update, this._pessimist );
        this._optimist = applyUpdate( update, this._optimist );

        // store the optimist value for retrieval
        if ( !equal( storeBeforeUpdate, this._optimist ) ) {
            this.value = this._optimist;
            this.trigger( 'change', this.value );
        }

        return this.value;
    },

    /**
     *
     * @return {*}
     */
    getValue() {
        return this.value;
    }
} );

export default OptimisticStore;



