// import Backbone from 'backbone';
import Modal from 'modal';

import 'jquery.tabs';
import 'jquery.quickview';

var modal = Modal.getInstance();

var app = upi.namespace( 'apps.productpage' ),

    PageController = Backbone.View.extend( {
        el: '#body-content',
        nodes: {
            '$priceModule': '#productCommerceContainer',
            '$productConfiguration': '#productConfiguration',
            '$productKitContents': '#productKitContents',
            '$productFamilyOverview': '#productFamilyOverview',
            '$productInformationWrapper': '#productInformationWrapper',
            '$supplementaryNav': '#supplementaryNav'
        },

        initNodes: function () {
            var nodes = this.nodes;
            for ( var node in nodes ) {
                if ( nodes.hasOwnProperty( node ) ) {
                    nodes[node] = this.$el.find( nodes[node] );
                }
            }
        },

        initialize: function () {

            // page initialization
            var self = this,
                nodes = this.nodes,
                backToTopHTML = '<div class="spacedBlock back-to-top"><a data-auto-scroll class="link2" href="#content-pane-scroller">Back to top <i class="icon-chevron-up"></i></a></div>';

            $( window ).smartresize( this.toggleLayout.bind( this ) );

            $( function () {
                self.initNodes();
                self.toggleLayout();

                nodes.$supplementaryNav.prepend( backToTopHTML );

                // Breakpoints
                if ( Modernizr.mq( upi.mq.max['screenMedium'] ) ) {
                    $( '.block.footnote' ).toContentToggle( {label: 'See Footnotes'} );
                }

                self.$el.on( 'click', '.returnPolicy, .pricingPolicy', function ( e ) {
                    e.preventDefault();
                    var href = e.currentTarget.getAttribute( 'href' );
                    if ( href ) {
                        modal.open( {target: href, width: 560} );
                    }
                } );

                $( '.tabSet' ).tabs( {controls: '.navList', set_height: true} );

                // set embed target for video containers
                nodes.$productFamilyOverview.find( '.videos a[href*="http://www.youtube.com"]' ).each( function () {
                    this.setAttribute( 'data-embed-target', 'img-ratio-16x9' );
                } );

                // QuickView items
                $( '#productContentContainer .tooltip-content-wrapper' ).QuickView( {position: 'bottom right'} );

                app.mediator.bind( 'toggle:customprinting', function ( doShow ) {
                    $( '.custom-printing-msg' ).toggle( doShow );
                } );

                // sticky navigation
                if ( !Modernizr.touchevents ) {
                    $( '#content-pane-scroller' ).on( 'scroll window:height', handlePageScroll );
                }

                function handlePageScroll () {
                    if ( this._to ) {
                        clearTimeout( this._to );
                        this._to = null;
                    }

                    this._to = setTimeout( positionProductNav, 10 );
                }

                var positionProductNav = (function () {
                    var $el = nodes.$supplementaryNav,
                        $container = $( upi.conf.SCROLL_CONTEXT ),
                        $page = $( '#page-content' ),
                        footerOffset = 150,
                        elHeight = $el.height(),
                        elTop = $el.offset().top;

                    return function positionProductNav () {
                        var scrollPos = $container.scrollTop(),
                            outOfRange = scrollPos >= $page.height() - elHeight - footerOffset,
                            inRange = scrollPos >= elTop && !outOfRange;
                        $el.toggleClass( 'fixed-top', inRange ).toggleClass( 'absolute-bottom', outOfRange );
                    };
                }());

                nodes.$productFamilyOverview.find( '.accordion' ).on( 'open:accordion-item', function ( data ) {
                    // reset inner components
                    data.item.find( '.tabSet' ).trigger( 'reset' );
                } );

                var $procedureNav = $( '#section-procedures .navList' );
                if ( $procedureNav.find( 'li' ).length <= 1 ) {
                    $procedureNav.hide();
                }

                // open accordion item
                nodes.$supplementaryNav.on( 'click', '.navList a', function ( e ) {
                    openMenuItem( $( this.hash ) );
                    toggleNavItem( $( this ) );
                } );

                function openMenuItem ( $menuItem ) {
                    if ( $menuItem[0] ) {
                        $menuItem.parents( '.accordion:first' ).trigger( {type: 'open:accordion', item: $menuItem} );
                    }
                }

                function toggleNavItem ( $item ) {
                    $item.parents( '.navList' ).find( 'li.selected' ).removeClass( 'selected' );
                    $item.parent( 'li' ).addClass( 'selected' );
                }

            } );

        },

        /**
         * toggleLayout
         * Responsive layout toggles configurator view between small and large screen devices
         */
        toggleLayout: function () {
            if ( Modernizr.mq( upi.mq.max.screenMedium ) ) {
                // move pricing module below configurator
                this.nodes.$priceModule.insertAfter( this.nodes.$productKitContents );
            }
            else {
                this.nodes.$priceModule.insertBefore( this.nodes.$productInformationWrapper );
            }
        },

        close: function () {
            app.mediator.unbind( 'toggle:customprinting' );
            this.remove();
        }
    } );

export default PageController;



