var dataLayer = upi.namespace( 'dataLayer' );

dataLayer.productConfigurator = upi.dataLayer.create( 'productConfigurator', {
    addedWithoutSelection: function () {
        this.trigger( 'configuratorAddWithNoSelection' );
    },
    // mapped to DTM dataElement - upiProdView
    // DTM data ref: upi.DTM.productConfigurator.productView
    setProductView: function ( sku ) {
        var prodViewData = {
            prodName: sku.getProductName().replace( ',', '' )
        };

        this.setData( 'productView', prodViewData ).queue( 'upiProdView' );
        return this;
    }
} );

export default dataLayer.productConfigurator;



